import longButton from "../../components/my-components/LongButton";
let cache = '';
let requesting = false;
export default {
    data() {
        return {
            code: ''
        }
    },
    components: {
        longButton
    },
    methods: {
        inputMoney(val) { //手动输入
            cache = val.detail.value;
        },
        submit() { //提交核销码
            if (!requesting) {
                requesting = true;
                this.closePageLoading();
                this.Toast('正在验证')
                this.Request('postSelfVerifyInfo', {
                    finish_code: cache
                }).then(res => {
                    requesting = false; 
                    if (res.error == 0) {
                        this.Toast('核销成功')
                    }
                }).catch(res => {
                    requesting = false; 
                    this.Toast(res.message)
                })
            }
        },
        checkVrCode() { //扫描获取二维码
            let that = this;
            uni.scanCode({
                success(res) {
                    cache = res.result;
                    that.code = cache;
                },
                fail(res) {
                    // console.log(res)
                }
            })
        },
        checkHistory() { //跳转历史核销记录
            uni.navigateTo({
                url: './historyRecord?from=selfVery'
            })
        }
    },
    onShow() {
        this.closePageLoading();
    },
    onLoad() {
        requesting = false;
    }
};
